import React from 'react';
import People from "../../controls/People/people";
import { Grid, Col } from 'react-grid-system';

import s from "./about.module.css";
import {useTranslation} from "react-i18next";

const About = (props) => {
    const { t } = useTranslation();
    return (
        <div className={s.about__container}>
            <h2 className={s.about__title}>{t('about.title')}</h2>
            <div className={s.about__block}>
                <div className={s.about__devs}>
                    <People
                        image={"https://avatars.githubusercontent.com/u/118774688?v=4"}
                        text={"Bogdan"}
                        link={"https://github.com/boogdann"}/>
                    <People
                        image={"data:image/jpeg;base64,"}
                        text={"Kate"}
                        link={"https://github.com/bondrrenko"}/>
                    <People
                        image={"https://avatars.githubusercontent.com/u/48926720?v=4"}
                        text={"Yagor"}
                        link={"https://github.com/Egor123qwe"}/>
                    <People
                        image={"https://avatars.githubusercontent.com/u/114881233?v=4"}
                        text={"Ilya"}
                        link={"https://github.com/tadeush504"}/>
                </div>
            </div>
        </div>
    );
};

export default About;